@media (max-width: 800px) {
  .schema__filters {
    -ms-flex-pack: start;
        justify-content: flex-start;
  }
}

.schema__platform-filter {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  width: 450px;
}
@media (max-width: 800px) {
  .schema__platform-filter {
    display: block;
  }
}

.schema__platform-dropdown {
  position: relative;
  top: 2px;
}

.schema__restore-default-button {
  font-size: 15px;
  margin-right: 5px;
}
@media (max-width: 800px) {
  .schema__restore-default-button {
    font-size: 12px;
  }
}
